<template>
  <view class="container">
    <!-- Ë¥¶Êà∑‰ΩôÈ¢ùÂç°Áâá -->
    <view class="balance-card">
      <view class="balance-info">
        <text class="balance-label">Ë¥¶Êà∑‰ΩôÈ¢ùÔºàÂÖÉÔºâ</text>
        <text class="balance-amount">{{ totalBalance }}</text>
      </view>
      <button class="btn-recharge" @click="recharge">ÂÖÖÂÄº</button>
    </view>

    <!-- ÂÖÖÂÄºËÆ∞ÂΩï -->
    <view class="record-list">
      <view class="list-header">
        <text class="header-title">ÂÖÖÂÄºËÆ∞ÂΩï</text>
      </view>
      
      <view 
        class="record-item card"
        v-for="(item, index) in recordList" 
        :key="index"
      >
        <view class="record-left">
          <view class="record-icon" :class="'status-' + item.payStatus">
            <text v-if="item.payStatus === '1'">üí∞</text>
            <text v-else-if="item.payStatus === '0'">‚è∞</text>
            <text v-else>‚Ü©</text>
          </view>
          <view class="record-info">
            <view class="record-title">
              <text v-if="item.payStatus === '1'">ÂÖÖÂÄºÊàêÂäü</text>
              <text v-else-if="item.payStatus === '0'">ÂæÖÊîØ‰ªò</text>
              <text v-else>Â∑≤ÈÄÄÊ¨æ</text>
            </view>
            <view class="record-time">{{ formatTime(item.payTime) }}</view>
            <view class="record-type">
              <text v-if="item.payType === '1'">ÂæÆ‰ø°ÊîØ‰ªò</text>
              <text v-else-if="item.payType === '2'">ÊîØ‰ªòÂÆù</text>
              <text v-else>ÂÖ∂‰ªñ</text>
            </view>
          </view>
        </view>
        
        <view class="record-right">
          <text class="amount" :class="'status-' + item.payStatus">
            {{ item.payStatus === '2' ? '-' : '+' }}{{ item.payAmount }}
          </text>
        </view>
      </view>
    </view>

    <!-- Á©∫Áä∂ÊÄÅ -->
    <u-empty 
      v-if="!loading && recordList.length === 0" 
      text="ÊöÇÊó†ÂÖÖÂÄºËÆ∞ÂΩï"
      mode="list"
    />

    <!-- Âä†ËΩΩ‰∏≠ -->
    <u-loading-icon v-if="loading" mode="circle" />
  </view>
</template>

<script>
import { getPayList } from '@/api/api.js'

export default {
  data() {
    return {
      loading: false,
      totalBalance: 0,
      recordList: []
    }
  },
  onLoad() {
    this.loadData()
  },
  onShow() {
    // ÊØèÊ¨°ÊòæÁ§∫È°µÈù¢Êó∂Âà∑Êñ∞Êï∞ÊçÆ
    this.loadData()
  },
  methods: {
    // Âä†ËΩΩÊï∞ÊçÆ
    async loadData() {
      try {
        this.loading = true
        const res = await getPayList({
          pageNum: 1,
          pageSize: 100
        })
        this.recordList = res.rows || []
        
        // ËÆ°ÁÆóÊÄª‰ΩôÈ¢ùÔºàÁÆÄÂåñÂ§ÑÁêÜÔºåÂÆûÈôÖÂ∫î‰ªéÂêéÁ´ØËé∑ÂèñÔºâ
        if (this.recordList.length > 0) {
          this.totalBalance = this.recordList[0].balance || 0
        }
      } catch (error) {
        console.error('Âä†ËΩΩÂÖÖÂÄºËÆ∞ÂΩïÂ§±Ë¥•', error)
      } finally {
        this.loading = false
      }
    },
    
    // ÂÖÖÂÄº
    recharge() {
      uni.showModal({
        title: 'ÂÖÖÂÄº',
        content: 'ËØ∑ËæìÂÖ•ÂÖÖÂÄºÈáëÈ¢ù',
        editable: true,
        placeholderText: 'ËØ∑ËæìÂÖ•ÈáëÈ¢ù',
        success: (res) => {
          if (res.confirm) {
            const amount = parseFloat(res.content)
            if (isNaN(amount) || amount <= 0) {
              uni.showToast({
                title: 'ËØ∑ËæìÂÖ•Ê≠£Á°ÆÁöÑÈáëÈ¢ù',
                icon: 'none'
              })
              return
            }
            
            // ËøôÈáåÂ∫îËØ•Ë∞ÉÁî®ÊîØ‰ªòÊé•Âè£
            uni.showToast({
              title: 'ÂÖÖÂÄºÂäüËÉΩÂºÄÂèë‰∏≠',
              icon: 'none'
            })
          }
        }
      })
    },
    
    // Ê†ºÂºèÂåñÊó∂Èó¥
    formatTime(time) {
      if (!time) return ''
      const date = new Date(time)
      const year = date.getFullYear()
      const month = String(date.getMonth() + 1).padStart(2, '0')
      const day = String(date.getDate()).padStart(2, '0')
      const hour = String(date.getHours()).padStart(2, '0')
      const minute = String(date.getMinutes()).padStart(2, '0')
      return `${year}-${month}-${day} ${hour}:${minute}`
    }
  }
}
</script>

